<template>
  <div class="component-contact">
    <div class="parallax">
      <div class="wrapper font-poppins">
        <h1>Contact</h1>
        <h2>Publessing listens to your opinions.</h2>
      </div>
    </div>
    <div class="comment part">
      <div class="wrapper">
        <div class="sm-title">코멘트</div>
        <p>프로그램에 대한 의견을 남겨주시면 검토하여 프로그램을 개선하겠습니다.</p>
        <Comment />
      </div>
    </div>
    <div class="email part">
      <div class="wrapper">
        <div class="sm-title">이메일</div>
        <div>이메일을 통해 문의사항을 보내주시면 발신 메일로 답변드리겠습니다.</div>
        <div class="content">
          <div class="row">
            <div class="col">
              <div class="form-group message">
                <label for="contactMessage">
                  <span>내용 </span>
                  <span class="color-point">*</span>
                  <span class="float-right">({{ state.contact.message.length }} / 1000)</span>
                </label>
                <textarea class="form-control focus-border-pointer" id="contactMessage" placeholder="Hey! It's cool!" @input="state.contact.message = $event.target.value" :disabled="state.loaded ? false : true"></textarea>
              </div>
            </div>
            <div class="col">
              <div class="name form-group">
                <label for="contactName">이름</label>
                <input type="text" class="form-control focus-border-pointer" id="contactName" placeholder="James" v-model="state.contact.name" @keyup.enter="send()" :disabled="state.loaded ? false : true" />
              </div>
              <div class="email form-group">
                <label for="contactEmailFrom">
                  <span>발신 메일 </span>
                  <span class="color-point">*</span>
                </label>
                <input type="email" class="form-control focus-border-pointer" id="contactEmailFrom" placeholder="name@example.com" v-model="state.contact.email" @keyup.enter="send()" :disabled="state.loaded ? false : true" />
              </div>
              <div class="email form-group">
                <label for="contactEmailTo">
                  <span>수신 메일</span>
                </label>
                <input type="email" class="form-control focus-border-pointer" id="contactEmailTo" placeholder="name@example.com" value="africalibrary21@gmail.com" readonly />
              </div>
              <div class="satisfaction form-group">
                <label for="contactSatisfaction">
                  <span>만족도 </span>
                  <span class="color-point">*</span>
                  <span class="float-right">{{ state.contact.comment }}</span>
                </label>
                <div class="icons row">
                  <div class="col text-right">
                    <i class="fa fa-frown-o" :class="{ 'active color-point': state.contact.point === '1', disabled: !state.loaded }" @click="point('1')"></i>
                  </div>
                  <div class="col text-center">
                    <i class="fa fa-meh-o" :class="{ 'active color-point': state.contact.point === '2', disabled: !state.loaded }" @click="point('2')"></i>
                  </div>
                  <div class="col">
                    <i class="fa fa-smile-o" :class="{ 'active color-point': state.contact.point === '3', disabled: !state.loaded }" @click="point('3')"></i>
                  </div>
                </div>
              </div>
              <div class="action">
                <button class="btn btn-point" @click="send()" :disabled="state.loaded ? false : true">
                  <span>{{ `발송 ${state.loaded ? "" : "중..."}` }}</span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive } from "@vue/reactivity";
import { computed } from "@vue/runtime-core";
import commLib from "../libs/commonLib";
import httpLib from "../libs/httpLib";
import { useStore } from "vuex";
import Comment from "../components/Comment";

export default {
  props: {
    callback: Function,
  },
  components: { Comment },
  setup() {
    const store = useStore();
    const state = reactive({
      contact: {
        name: "",
        email: "",
        message: "",
        point: "3",
        comment: computed(() => {
          if (state.contact.point === "3") {
            return "It's Cool";
          } else if (state.contact.point === "2") {
            return "Not bad";
          } else {
            return "Bad";
          }
        }),
      },

      loaded: true,
    });

    const point = (point) => {
      if (!state.loaded) {
        return;
      }

      state.contact.point = point;
    };

    const send = () => {
      if (!state.loaded) {
        return;
      }

      if (!state.contact.message) {
        commLib.message.show("warning", "내용을 입력해주세요.");
        document.querySelector("#contactMessage").focus();
      } else if (!state.contact.email) {
        commLib.message.show("warning", "이메일 주소를 입력해주세요.");
        document.querySelector("#contactEmailFrom").focus();
      } else if (!commLib.isValidEmail(state.contact.email)) {
        commLib.message.show("warning", "이메일 주소의 형식이 올바르지 않습니다.");
        document.querySelector("#contactEmailFrom").focus();
      } else if (state.contact.message.length > 1000) {
        commLib.message.show("warning", "메시지는 1000자를 넘을 수 없습니다.");
      } else if (state.contact.name && state.contact.name.length > 50) {
        commLib.message.show("warning", "이름은 50자를 넘을 수 없습니다.");
      } else if (state.contact.email.length > 100) {
        commLib.message.show("warning", "이메일 주소는 100자를 넘을 수 없습니다.");
      } else if (confirm("이메일을 발송하시겠습니까?")) {
        state.loaded = false;

        httpLib
          .post("/api/email", state.contact)
          .then(() => {
            commLib.message.show("success", "이메일을 발송하였습니다.");
            state.loaded = true;
            state.contact.name = "";
            state.contact.email = "";
            state.contact.message = "";
            state.contact.point = "3";
            document.querySelector("#contactMessage").value = "";
          })
          .catch(() => {
            commLib.message.show("error", "오류가 있습니다.");
            state.loaded = true;
          });
      }
    };

    if (store.state.account.login) {
      state.contact.email = store.state.account.email;
    }

    return { store, state, point, send };
  },
};
</script>
<style lang="scss" scoped>
.component-contact {
  padding-bottom: 100px;

  .parallax:before {
    background-image: url(/img/post-impressionist-1424183_1280.jpg);
  }

  .content {
    padding-top: 35px;
  }

  .part {
    &.comment {
      p {
        margin-bottom: 20px;
      }
    }

    &.email {
      .row {
        > .col {
          .message {
            margin-bottom: 35px;
          }

          .email {
            margin: 35px 0;
          }

          .satisfaction {
            .icons {
              margin-top: 25px;

              i {
                cursor: pointer;
                padding: 5px;
                font-size: 42px;
                opacity: 0.25;

                &:hover {
                  opacity: 0.5;
                }

                &.active {
                  opacity: 1;
                }
              }
            }
          }

          label {
            display: block;
            font-size: 14px;
          }

          textarea {
            border-radius: 0;
            height: 492px;
            width: 100%;
            resize: none;
            padding: 15px;
            box-shadow: none;
          }

          input {
            border-radius: 0;
            height: 50px;
            box-shadow: none;
          }

          .action {
            padding-top: 15px;

            button {
              width: 100%;
              border-radius: 0;
              height: 50px;
              font-size: 13px;
            }
          }
        }
      }
    }
  }
}
</style>
